



























































































































































































































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CDatetime from '@/components/links/CDatetime.vue';
import moment from 'moment';

@Component({
  components: {
    CDatetime,
  },
  head: {
    title: {
      inner: '現在日時',
    },
  },
})
export default class Now extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataNowMoment: any = moment(new Date());
  private dataShouldUpdate: boolean = false;
  private dataTimerId: any = 0;

  // Lifecycle hook
  created(): void {
    this.dataShouldUpdate = true;
    this.switchUpdating();
  }

  // Methods
  private listenOnCheckbox($event: Event): void {
    this.dataShouldUpdate = (<HTMLInputElement>(<any>$event).target).checked;
    this.switchUpdating();
  }
  private switchUpdating(): void {
    if (this.dataShouldUpdate) {
      this.clock();
    } else {
      this.stopClock();
    }
  }
  private clock(): void {
    this.dataTimerId = setInterval((): void => {
      this.dataNowMoment = moment(new Date());
    }, 10);
  }
  private stopClock(): void {
    clearInterval(this.dataTimerId);
  }
}
